import {style_state, alloc_state as alloc, mode_state} from '../../state/i.js';
import {
    list,
    scrollbar_thumb_x_style,
    scrollbar_thumb_y_style,
    html_style,

    elements,
    list_inner_style,
} from '../../elems/i.js';

import {
    on_window_resize_template,
} from '../../on/i.js';

import {
    number_clamp,
    set_scroll,
    scrollbar_thumb_x_transform,
    scrollbar_thumb_y_transform,
    set_list_scroll_x,
    set_list_scroll_y,

} from '../../f/i.js';
import {window_event_object} from '../../conf/i.js';


export default (
    (new_zoom) => {
        var
            row_height = (style_state.row_height = (style_state.row_height_default * new_zoom)),

            i = alloc.offset_loaded_elements,
            l = alloc.length_loaded_elements,

            ratio = 0,

            scroll_left = list.scrollLeft,
            scroll_top = list.scrollTop,

            zoom_prev = style_state.zoom,

            row_width_mode = mode_state.row_width,

            list_width = style_state.list_width,
            list_height = style_state.list_height,

            content_height = style_state.content_height,
            content_width = style_state.content_width,

            thumb_x_size = style_state.thumb_x_size,
            thumb_y_size = style_state.thumb_y_size,

            loaded_height = 0,

            d = 0
        ;

        while (i < l) {
            elements[i].style.top = `${loaded_height}px`;
            loaded_height += row_height;
            i++;
        };

        return void (
            (
                ratio = (
                    (style_state.zoom = new_zoom)
                    / (zoom_prev)
                )
            ),

            html_style.setProperty("--row-height",`${row_height}px`),
            html_style.setProperty(
                "--font-size",
                `${style_state.font_size = (style_state.font_size_default * new_zoom)}px`
            ),

            (list_inner_style.height = `${
                content_height =
                style_state.content_height = (
                    (style_state.loaded_height = loaded_height)
                    + (
                        style_state.extra_scroll_height = (
                            list_height-(row_height-style_state.list_top)
                        )
                    )
                )
            }px`),

            (row_width_mode === 0)
            &&
            (
                list_inner_style.width = `${
                    content_width =
                    style_state.content_width = (
                        (style_state.loaded_width * new_zoom)
                        + (style_state.extra_scroll_width)
                    )
                }px`
            ),

            on_window_resize_template[row_width_mode](
                window.innerWidth,
                window.innerHeight,

                list_width,
                list_height,
                
                thumb_x_size,
                thumb_y_size,
            ),

            (list.scrollLeft = (
                (
                    ((d = (thumb_x_size * (1 - style_state.thumb_x_scale))) > 0)
                    ? (scroll_left - (((style_state.excess_x) / d) * content_width))
                    : (scroll_left)
                )
                * ratio
            )),
            (list.scrollTop = (
                (
                    ((d = (thumb_y_size * (1 - style_state.thumb_y_scale))) > 0)
                    ? (scroll_top - (((style_state.excess_y) / d) * content_height))
                    : (scroll_top)
                )
                * ratio
            ))
        )
    }
);
